@tailwind base;
@tailwind components;
@tailwind utilities;
.lds-ring {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px; }

.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 64px;
  height: 64px;
  margin: 8px;
  border: 8px solid #fff;
  border-radius: 50%;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #B9B9B9 transparent transparent transparent; }

.lds-ring div:nth-child(1) {
  animation-delay: -0.45s; }

.lds-ring div:nth-child(2) {
  animation-delay: -0.3s; }

.lds-ring div:nth-child(3) {
  animation-delay: -0.15s; }

@keyframes lds-ring {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

body {
  font-family: 'Noto Sans TC', 'sans-serif';
  font-size: 16px; }

@media md {
  .material-icons.md\:hidden {
    @apply hidden; } }

[data-js="offcanvas"] {
  @apply h-0 overflow-hidden; }

[data-offcanvasBurger="active"] {
  @apply text-primary; }

[data-chartBtn="active"] {
  @apply opacity-75 cursor-default; }

[data-chart="show"] {
  @apply block; }

[data-chart="hidden"] {
  @apply hidden; }

[data-hasOrder="show"] {
  @apply flex; }

[data-hasOrder="hidden"] {
  @apply hidden; }

[data-popUp="show"] {
  @apply flex; }

[data-popUp="hidden"] {
  @apply hidden; }

[data-loading="show"] {
  @apply flex; }

[data-loading="hidden"] {
  @apply hidden; }
